@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cuprum:ital@1&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  /* background-image: url('https://cdn.discordapp.com/attachments/870313330319949895/870363741823324160/2021-07-30_02.52.29.png');
  background-color: #0f0f0f; */

  background: linear-gradient(rgba(15, 15, 15, 0.7), #0f0f0f 100%),
    url('https://cdn.discordapp.com/attachments/870313330319949895/870363741823324160/2021-07-30_02.52.29.png');
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-position: top;
}

a {
  text-decoration: none;
}
.container {
  padding: 30px;
  text-align: center;
}

.serverName {
  display: inline-block;
  padding: 50px;
}

.name {
  font-size: 500%;
  font-family: 'Comfortaa', cursive;
  color: #2bffc6;
}

.tagline {
  font-size: 200%;
  font-family: 'Cuprum', sans-serif;
  color: #f9f9f9;
}

#ipButton {
  cursor: pointer;
}

.button {
  border-radius: 25px;
  text-align: center;
  font-family: 'Comfortaa', cursive;
  vertical-align: middle;
  line-height: 50px;
  font-size: 200%;
  text-decoration: none;
  color: rgb(0, 0, 0);
  background-color: #2bffc6;
  height: 50px;
  position: relative;
  margin-bottom: 5px;
  transition: 0.2s linear; 
}

.button:hover {
  background-color: #22CC9E;
}

.navigationContainer {
  padding: 30px;
  text-align: center;
}
.navigation {
  display: inline-block;
  margin: 30px;
  width: 50%;
}

